body {
  margin: auto;
  line-height: 1.6em;
}
body {
  --max-width: 869px;
}
#page-content {
  max-width: var(--max-width);
  margin: auto;
  padding-left: 14px;
  padding-right: 14px;
}

:root {
  /* rgb(0, 174, 65) == #00ae41 */
  --color-green-rgb: 0, 174, 65;
  /* rgb(49, 52, 61) == #31343d */
  --color-black-rgb: 49, 52, 61;
  --color-black: rgb(var(--color-black-rgb));
  --color-green: rgb(var(--color-green-rgb));
}

body {
  color: var(--color-black);
}
a {
  color: inherit;
}

table {
  border-collapse: collapse;
}
th,
td {
  border: 1px solid #aaa;
  padding: 10px;
}

/*
 * Fonts
 */
body {
  /* `Custom Open Sans`: loaded font
   * `Open Sans`: system font
   */
  font-family: "Custom Open Sans", "Open Sans", "Helvetica Neue", Helvetica,
    sans-serif;
}
.quote-font {
  font-style: italic;
  font-family: "Custom Georgia", Georgia, Times, "Times New Roman", serif;
  /*
  font-family: serif;
  font-family: "Times New Roman";
  font-family: Times;
  font-family: Georgia;
  font-family: "Custom Georgia";
  */
}
.title-font,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "AirbnbCereal", sans-serif;
}
h1,
h2,
h3 {
  font-weight: bold;
}

h1 + h2,
h1 + h3,
h2 + h3,
h3 + h4 {
  margin-top: 0;
}

h3 {
  font-size: 1.1em;
  line-height: 2em;
  margin-bottom: 1.4em;
}
h3,
.h3-margin-top {
  margin-top: 45.76px;
}
h2 {
  font-size: 1.4em;
  line-height: 2em;
  margin-bottom: 1.4em;
}
h2,
.h2-margin-top {
  margin-top: 62.72px;
}
h1 {
  font-size: 2em;
  line-height: 1.2em;
  margin-bottom: 0.87em;
}
h1,
.h1-margin-top {
  margin-top: 12.8px;
}

.values-table th {
  font-weight: normal;
}
.values-table tr td:first-child {
  text-align: left;
}
.gauge {
  width: 60px;
}
/*
.values-table {
  outline: 1px solid red;
}
*/
@media (max-width: 599px) {
  .values-table {
    table-layout: fixed;
    font-size: 0.75em;
  }
  .values-table th,
  .values-table td {
    padding: 1px;
  }
  .gauge {
    width: 24px;
  }
}

.legal-doc {
  font-size: 0.9em;
  line-height: 1.2em;
  /*
  fontWeight: lighter;
  */
}
.legal-doc h1 {
  margin-bottom: 27px;
}

code {
  font-size: 1em;
  background-color: #eee;
  padding: 2px 7px;
  border-radius: 4px;
}
.code-block {
  white-space: pre-wrap;
  background-color: #f6f8fa;
  border-radius: 3px;
  padding: 16px;
}

li {
  margin-top: 2px;
}

ul.long-bullets li {
  margin-top: 0.6em;
}

/*
 * Background Colors
 *
 */
body {
  --page-background: white;
}
body {
  position: relative;
}
body:before,
body:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
body:before {
  z-index: -2;
  background-color: var(--page-background);
}
body:after {
  z-index: -1;
  background-color: var(--menu-background-color);
}
#page-content-container {
  background-color: var(--page-background);
}
#page-content-container {
  padding-bottom: 70px;
}

/*
 * Layout
 *
*/
html {
  height: 100%;
}
body {
  min-height: 100%;
  overflow-y: scroll;
}

/*
 * Buttons
 *
 */
.lsos-button {
  --btn-color: var(--color-green);
  background-color: var(--btn-color);
  padding: 0 20px;
  padding-top: 8px;
  padding-bottom: 11px;
  margin: 0 5px;
  color: white;
  border: 0;
  text-decoration: none;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 6px;
  /*
  box-shadow: 2px 2px 4px #ccc;
  */
  min-height: 40px;
}
.lsos-button--one-liner {
  padding: 8px 20px;
  margin: 0;
}
.lsos-button--secondary {
  background-color: white;
  color: var(--btn-color);
  border: 2px solid var(--btn-color);
}
.lsos-button--action-alone,
.lsos-button--action-top,
.lsos-button--action-bottom {
  font-size: 1.45em;
}
.lsos-button--action-alone,
.lsos-button--action-top {
  padding-bottom: 3px;
}
.lsos-button--action-alone,
.lsos-button--action-bottom {
  padding-top: 2px;
}
.lsos-button--desc {
  font-size: 0.86em;
  line-height: 1.3em;
}

.semibold {
  font-weight: 500;
}
